const state = {
    notifications:[]
}

const getters = {
    getNotifications:state => state.notifications

 };

const actions = {

};

const mutations = {
    pushNotification(state,notification){
        notification.id = (Math.floor(Math.random() * 9999) +1) /* Rastgele bir id vermemiz gerekiyor ki kullanc sonradan istedii bir notification' ID'ye gre silebilsin.*/
        state.notifications.push(notification);
    },
    deleteNotification(state,id){
        state.notifications = state.notifications.filter((item) => item.id !== id);
    }
};

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
};